}

.rec.rec-carousel-wrapper.services-carousel {
  .rec.rec-item-wrapper {
    padding: 5px !important;
    height: 100%;
  }
  .rec.rec-arrow {
    padding: 0;
    width: fit-content;
    height: fit-content;
    background-color: transparent;
    color: rgba(255, 255, 255, 0.25);
    box-shadow: none;
    &:hover {
      scale: 1.5;
      background-color: transparent;
      color: #ffce89;
      box-shadow: none;
    }
  }
  .rec.rec-pagination {
    .rec.rec-dot {
      box-shadow: 0 0 1px 3px rgba(255, 206, 137, 0.25);
    }
    .rec.rec-dot.rec-dot_active {
      background-color: #ffce89;
      box-shadow: 0 0 1px 3px #ffce89;
    }
  }
}

$primary: #ffce89;
.form__group {
  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
}
.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  outline: 0;
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.5);
  padding: 7px 20px;
  margin: 5px 0;
  background: transparent !important;
  transition: border-color 0.2s;

  &::placeholder {
    color: transparent;
  }

  &:placeholder-shown ~ .form__label {
    font-size: 1rem;
    cursor: text;
    top: 20px;
  }
}

.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
}

.form__field:focus {
  ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 0.8rem;
    color: $primary;
    font-weight: 900;
  }
  color: white;
  border-color: $primary;
  padding-bottom: 6px;
  border-image-slice: 1;
}
/* reset input */
.form__field {
  &:required,
  &:invalid {
    box-shadow: none;
  }
}

.text-area {
  margin: 13px 0 0 0;
  height: 130px;
  overflow-y: scroll;
  resize: none;
  &::-webkit-scrollbar {
    width: 10px;
  }
  &::-webkit-scrollbar-track {
    background-color: transparent;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #7d7d7d;
    border-radius: 100px;
  }
}
